/** Small screens (typically phone) */
@media only screen and (max-width: 800px) {

  /** changing this scales all components using rem for sizing */
  html {
    font-size: 8.5px;
  }

  .dashboard {
    display: flex;
    flex-direction: column;
    gap: 4rem;
    padding: 3rem;
  }

  .md-meeting-widget {
    max-height: 70vh;
  }

  .home-button-group {
    gap: 2rem 0;
  }

  .call-controls {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
  }

  .vertical-in-portrait {
    flex-direction: column;
  }

  /* make dialog fullscreen on mobile */
  .dialog {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 0;
  }

  .dialog .md-close {
    display: block;
  }
}

/** Large screens (laptops) */
@media only screen and (min-width: 1600px) {

  html {
    font-size: 12px;;
  }
}