* {
  box-sizing: border-box;
}

html, body {
  padding: 0;
  margin: 0;
  height: 100%;
}

.md-page.start {
  background-size: cover;
  background-image: url('../images/wallpaper-start.png');
}

#root, main {
  position: fixed;
  width: 100%;
  height: 100%;
}

.wallpaper {
  background-image: url('../images/wallpaper.jpeg');
  background-size: cover;
}

.md-top-bar button {
  font-size: 2.4rem;
  font-weight: 700;
  color: var(--mds-color-theme-theme-button-secondary-normal);
}

.dashboard {
  width: 100%;
  height: 100%;
  display: grid;
  /* grid-template-columns: 1fr 1fr; */
  grid-template-columns: repeat(2, minmax(0, 1fr));
  align-items: center;
  justify-items: center;
  max-width: 120rem;
  padding: 5.4rem;
}

.dashboard.single {
  grid-template-columns: 1fr;
}

.home-button-group {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 6rem 3rem;
}

.transparent {
  background: transparent;
}

.connection-fade {
  backdrop-filter: blur(5px);
  background-color: #0003;

}

.link {
  text-decoration: underline;
}

.link:hover {
  text-decoration: none;
}

.volume-controls {
  position: absolute;
  bottom: 2rem;
  right: 2.4rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 18rem;
  border-radius: 10rem;
  border: 2px solid var(--mds-color-theme-common-outline-button-normal);
}

.volume-controls .indicator {
  transition: opacity 0.3s;
  opacity: 0;
}

.volume-controls .indicator.show {
  opacity: 1;
}

ul li {
  margin: 0.5em 0;
}

.center {
  display: flex;
  justify-content: center;
  align-items: center;
}

.center.row {
  flex-direction: row;
}

.center.column {
  flex-direction: column;
}

.call-controls {
  gap: 3.6rem;
  padding: 0 3.6rem;
}

.call-controls .md-button-app {
  width: 11rem;
}

.call-history-item.missed {
  color: var(--mds-color-theme-indicator-attention);
}

.call-history-item.incoming .md-icon {
  transform: rotate(225deg);
}

.call-history-item .md-avatar {
  color: var(--mds-color-theme-text-primary-normal);
}

.call-history-item.outgoing .md-icon {
  transform: rotate(45deg);
}

textarea.logs {
  width: 100%;
  min-height: 40rem;
}

.selfview-pip-grid {
  position: relative;
  display: grid;
  width: 336px;
  height: 212px;
  padding: 8px;
  background-color: var(--mds-color-theme-background-solid-secondary-normal);
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr;
  gap: 8px;
  border-radius: 12px;
}

.selfview-pip-grid.fullscreen::after {
  font-family: MomentumFontIcon;
  font-size: 150px;
  content: "\f7ee";
  color: white;
  position: absolute;
  left: 90px;
  top: 28px;
}

.pip-pos {
  background-color: var(--mds-color-theme-background-secondary-normal);
  border-radius: 8px;
  cursor: pointer;
  border: 0;
}

.pip-pos.selected {
  border: 2px solid #666;
  background-color: inherit;
  position: relative;
}

.pip-pos.selected::after {
  font-family: MomentumFontIcon;
  font-size: 30px;
  content: "\f7ee";
  color: white;
  position: absolute;
  left: 32px;
  top: 14px;
}

.share-tray-list {
  overflow-x: auto;
}

.share-tray-item {
  padding: 2.4rem;
  border-radius: 2.4rem;
  width: 33rem;
  height: 40rem;
  cursor: pointer;
  flex-shrink: 0;
}

.share-tray-item.selected {
  background-color: var(--mds-color-theme-background-secondary-normal);
}

.share-tray-item:not(.selected) button {
  display: none;
}

.layout-list {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 24px 12px;
}

.layout-list button {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: transparent;
  border: 0;
  color: var(--mds-color-theme-text-secondary-normal);
  width: 184px;
  padding: 12px 0;
  border-radius: 16px;
}

.layout-list button .md-icon {
  color: var(--mds-color-theme-text-primary-normal);
}

.layout-list button.selected {
  background-color: var(--mds-color-theme-button-secondary-pressed);
  color: var(--mds-color-theme-text-primary-normal);
}

.reactions {
  position: absolute;
  bottom: 12vh;
  left: calc(50% - 300px);
  background-color: #2229;
  padding: 0 32px;
  border-radius: 3em;
  gap: 24px;
  border: 2px solid #333;
}

.reaction-button {
  font-size: 4.0rem;
  position: relative;
}

.reactions button {
  background-color: transparent;
  border: 0;
  padding: 12px 0;
  cursor: pointer;
}

.reaction-ghost {
  position: absolute;
  top: -0.5em;
  left: 0;
  opacity: 0;
  pointer-events: none;
  z-index: 0;
}

.reaction-ghost.show {
  animation: show-reaction 0.6s both linear;
}

textarea {
  border-radius: 12px;
  background-color: #333;
  border: 2px solid #666;
  color: #aaa;
  font-size: 1.4rem;
  padding: 12px;
}

.back-to-call-bar {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 11rem;
  padding: 0 3rem;
  font-size: 2rem;
  background-color: #121212cc;
  color: white;
}

@keyframes show-reaction {
  0% {
    opacity: 0.8;
    top: -0.5em;
  }
  70% {
    opacity: 0.8;
  }
  100% {
    top: -2.6em;
    opacity: 0;
  }
}