* {
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
  /* remove ugly built-in touch highlight */
  -webkit-tap-highlight-color:  rgba(255, 255, 255, 0);
  -webkit-touch-callout: none;
  -webkit-user-drag: none;
  -webkit-user-select: none !important;
}

@font-face {
  font-family: Inter;
  src: url('../fonts/Inter-VariableFont.ttf');
}

@font-face {
  font-family: MomentumFontIcon;
  src: url('../fonts/MomentumFontIcon.woff2');
}

/**
 * Set the base font size of the document to be 10px. This means all values can be scaled
 * using rem, without complicated multiplying
 * eg border radius of 12px can be defined as 1.2rem
 * It also means the whole ui can be scaled for different form factors by just adjusting the base size
 */
html {
  font-size: 10px;
}

body {
  font-family: Inter;
  font-size: 16px;
  background-color: var(--mds-color-theme-background-solid-primary-normal);
  color: var(--mds-color-theme-text-primary-normal);
}

.md-hlist {
  display: flex;
  flex-direction: row;
  gap: 1.2rem;
}

.md-vlist {
  display: flex;
  flex-direction: column;
  gap: 1.2rem;
}

.md-icon {
  font-family: MomentumFontIcon;
  font-style: normal;
  color: inherit;
  padding: 0;
  display: inline-block;
  margin: 0 auto;
  overflow: hidden;
  background-size: contain;
}

.md-icon.webex {
  background-image: url('../logos/webex.svg');
}

.md-icon.googlemeet {
  background-image: url('../logos/google-meet.svg');
}

.md-icon.zoom {
  background-image: url('../logos/zoom.svg');
}

.md-icon.microsoftteams {
  background-image: url('../logos/microsoft-teams.svg');
}

.md-icon.slido {
  background-image: url('../logos/slido.svg');
}

.md-text {
  font-size: 2.0rem;
  text-align: center;
}

.md-one-line {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  max-width: 100%;
  min-width: 0;
}

.md-text[data-type=title] {
  font-size: 2.4rem;
  font-weight: 700;
}

.md-text[data-type=message] {
  color: var(--mds-color-theme-text-secondary-normal);
  font-weight: 500;
}

.md-text[data-type=error] {
  color: var(--mds-color-theme-common-text-error-normal);
}

.md-text[data-type=success] {
  color: var(--mds-color-theme-common-text-success-normal);
}


.md-list {
  display: flex;
  flex-direction: column;
  gap: 12px;
  align-items: center;
  max-width: 100%;
}

.md-horizonal {
  flex-direction: row;
}

.md-center-along {
  justify-content: center;
}

.md-start-across {
  align-items: flex-start;
}

.md-grow {
  flex-grow: 1;
}

.md-text-input {
  background-color: transparent;
  position: relative;
  border: 2px solid var(--mds-color-theme-outline-primary-normal);
  border-radius: 12px;
  font-size: 2.0rem;
  display: flex;
  align-items: center;
  color: var(--mds-color-theme-text-primary-normal);
}


.md-text-input:focus-within {
  background-color: var(--mds-color-theme-background-primary-active);
  border: 2px solid var(--mds-color-theme-outline-input-active);
}

.md-text-input[data-disabled=true] {
  background-color: var(--mds-color-theme-background-primary-disabled);
  color: var(--mds-color-theme-text-primary-disabled);
}

.md-text-input input {
  border: 0;
  height: 100%;
  width: 100%;
  padding: 16px 24px;
  padding-right: 40px;
  background-color: transparent;
  color: inherit;
  outline: 0;
}

.md-erase {
  opacity: 0;
  position: absolute;
  top: 0;
  right: 6px;
  height: 100%;
  background-color: transparent;
  border: 0;
  color: inherit;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  pointer-events: none;
}

.md-text-input input:focus + .md-erase {
  opacity: 1;
  pointer-events: all;
}

.md-button {
  font-size: 2rem;
  background-color: transparent;
  border: 0;
  color: var(--mds-color-theme-button-normal);
  display: flex;
  align-items: center;
  gap: 8px;
}

.md-button-pill {
  font-weight: 500;
  height: 6rem;
  border-radius: 3rem;
  padding: 0 3rem;
  font-size: 2rem;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 8px;
  border: 0;
  transition: background-color 0.2s, transform 0.2s;
}

/** element starts with md- */
[class^='md-'][data-type=primary] {
  color: var(--mds-color-theme-button-inverted-normal);
  background-color: var(--mds-color-theme-button-primary-normal);
}


.md-button-pill:active {
  background-color: var(--mds-color-theme-button-primary-pressed);
}

.md-button-pill[data-type=secondary],
.md-button-pill[data-type=ghost] {
  border: 2px solid var(--mds-color-theme-common-outline-button-normal);
  background-color: var(--mds-color-theme-button-secondary-normal);
  color: var(--mds-color-theme-theme-button-secondary-normal);
}

.md-button-pill[data-type=ghost] {
  border: 0;
}


.md-button-pill[data-color=join] {
  background-color: var(--mds-color-theme-button-join-normal);
  color: var(--mds-color-theme-common-text-white);
}

.md-button-pill[data-color=share] {
  background-color: var(--mds-color-theme-common-control-share-content-active);
  color: var(--mds-color-theme-common-text-white);
}

.md-button-pill[data-color=cancel] {
  background-color: var(--mds-color-theme-button-cancel-normal);
  color: var(--mds-color-theme-common-text-cancel);
}

.md-button[data-color=cancel][data-type=secondary] {
  color: var(--mds-color-theme-common-text-error-normal);
}

.md-button-pill:disabled {
  background-color: var(--mds-color-theme-button-primary-disabled);
  color: var(--mds-color-theme-text-primary-disabled);
  cursor: default;
}

.md-button-row {
  gap: 2.4rem;
  width: 100%;
  padding: 1.6rem;
  border-radius: 12px;
}

.md-button-row:active {
  background-color: var(--mds-color-theme-background-primary-active);
}

.md-button-circle {
  aspect-ratio: 1 / 1;
  padding: 0;
  border-radius: 50%;
  overflow: hidden;
}

.md-box {
  display: flex;
}

.md-wide {
  width: 100%;
}

.md-row {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 12px;
}

.md-row.md-left {
  justify-content: flex-start;
}

.md-row.md-right {
  justify-content: flex-end;
}

.md-row.md-space {
  justify-content: space-between;
}

.md-row.md-top {
  align-items: flex-start;
}

.md-row.md-bottom {
  align-items: flex-end;
}

.md-scrollable-vertical {
  overflow-y: auto;
  scrollbar-color: #333 #444;
  scrollbar-width: thin;
}

.md-avatar {
  height: 48px;
  aspect-ratio: 1 / 1;
  background-color: var(--mds-color-theme-avatar-default);
  border-radius: 50%;
  display: flex;
  font-weight: 700;
  align-items: center;
  justify-content: center;
  background-size: contain;
}

.md-checkbox {
  position: relative;
  width: 48px;
  height: 24px;
  border-radius: 24px;
  border: 0;
  background-color: var(--mds-color-theme-control-inactive-normal);
}

.md-checkbox.md-selected {
  background-color: var(--mds-color-theme-control-active-active);
}

.md-checkbox:after {
  content: "";
  position: absolute;
  top: 30%;
  left: 20%;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background-color: white;
}

.md-checkbox.md-selected:after {
  background-color: black;
  left: unset;
  right: 20%;
}

/** Only add hover state when device type supports it. otherwise you get 'stuck' hover on mobile */
@media (hover: hover) {

  .md-text-input:hover:not([disabled]) {
    background-color: var(--mds-color-theme-background-primary-active);
  }

  .md-button-pill:hover:not([disabled]) {
    background-color: var(--mds-color-theme-button-primary-hover);
  }

  .md-button-pill[data-type=secondary]:hover:not([disabled]),
  .md-button-pill[data-type=ghost]:hover:not([disabled]) {
    background-color: var(--mds-color-theme-button-secondary-pressed);
  }

  .md-button-pill[data-color=join]:hover:not([disabled]) {
    background-color: var(--mds-color-theme-button-join-pressed);
  }

  .md-button-pill[data-color=cancel]:hover:not([disabled]) {
    background-color: var(--mds-color-theme-button-cancel-pressed);
  }

  .md-button-row:hover:not([disabled]) {
    background-color: var(--mds-color-theme-background-primary-active);
  }  

  .md-button-app .md-button-pill:hover {
    transform: scale(0.98);
  }
  
}


.md-spinner {
  animation-name: spin-animation;
  animation-duration: .75s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}

@keyframes spin-animation {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}