.md-page {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  flex-direction: column;
  background: var(--mds-color-theme-background-gradient-primary);
}

.md-top-bar, .md-bottom-bar {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100px;
  width: 100%;
  padding: 0 24px;
  flex-shrink: 0;
}

.md-button-app {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1.2rem;
  /* height: 150px; */
  width: 15rem;
  color: var(--mds-color-theme-common-text-white);
}

.md-button-app .md-button-pill {
  background-color: rgba(38, 38, 38, 0.90);
  color: var(--mds-color-theme-common-text-white);
  transform: scale(1);
  box-shadow: 0px 0px 1px 0px rgba(0, 0, 0, 0.12), 0px 24px 48px 0px rgba(0, 0, 0, 0.20);
}

.md-button-app .md-button-pill:hover {
  background-color: rgba(42, 42, 42, 0.90);
}

.md-button-app .md-button-pill:active {
  transform: scale(0.96);
}

.md-close {
  position: absolute;
  top: 2rem;
  right: 2.4rem;
}

/* hide dialog close button, only show on mobile */
.dialog .md-close {
  display: none;
}

.md-hide-call {
  position: absolute;
  top: 2rem;
  left: 2.4rem;
}

.md-meeting-widget {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 3.6rem;
  max-width: 60rem;
}

.md-meeting-info {
  display: grid;
  grid-template-columns: 1fr 0fr;
  width: 100%;
}

.md-meeting-status {
  color: var(--mds-color-theme-common-text-success-normal);
  font-weight: 600;
}

.md-keypad {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 2.4rem;
}

.md-keypad button {
  height: 80px;
  aspect-ratio: 1 / 1;
  border-radius: 80px;
  border: 2px solid var(--mds-color-theme-common-outline-button-normal);
  background-color: transparent;
  color: var(--mds-color-theme-text-primary-normal);
  font-size: 36px;
  font-weight: 400;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  padding-top: 12px;
  overflow: hidden;
}

.md-keypad .md-numeric {
  line-height: 1em;
}

.md-keypad label {
  font-size: 12px;
  color: var(--mds-color-theme-text-secondary-normal);
  text-transform: uppercase;
}

.md-keypad button:active {
  background-color: var(--mds-color-theme-background-primary-active);
}
