.fade {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  padding: 64px;
  background-color: #0008;
  display: flex;
  justify-content: center;
  align-items: center;
}

.dialog {
  background-color: black;
  border-radius: 24px;
  min-width: min(480px, 90vw);
  border: 2px solid #fff6;
  color: white;
  padding: 32px;
  /* padding-bottom: 80px; */
  max-height: 100%;
  max-width: 100%;
  overflow-y: auto;
  scrollbar-color: #222 #333;
  position: relative;
}

.notification {
  background-color: black;
  border-radius: 24px;
  min-width: min(600px, 90vw);
  border: 2px solid #fff6;
  color: white;
  padding: 16px;
}